<template>
  <div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>
    <!-- 新增商品按鈕 -->
    <div class="text-right">
      <button class="btn btn-primary mt-4" @click="openModal(true)">建立新的優惠券</button>
    </div>
    <!-- 優惠券列表 -->
    <table class="table mt-4">
      <thead>
        <th>名稱</th>
        <th width="200">折扣百分比</th>
        <th width="200">到期日</th>
        <th width="150">是否啟用</th>
        <th width="150">編輯</th>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date | date }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
          <div class="btn-group" role="group" aria-label="Basic example">
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)">編輯</button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelModal(item)">刪除</button>
          </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination v-if="pagination.total_pages>1" :pagination="pagination" @pageChange='getCoupons'></Pagination>

    <!-- 新增/修改優惠券 Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">新增優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title">標題</label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                placeholder="請輸入標題">
            </div>
            <div class="form-group">
              <label for="coupon_code">優惠碼</label>
              <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                placeholder="請輸入優惠碼">
            </div>
            <div class="form-group">
              <label for="due_date">到期日</label>
              <input type="date" class="form-control" id="due_date"
              v-model="due_date">
            </div>
            <div class="form-group">
              <label for="price">折扣百分比</label>
              <input type="number" class="form-control" id="price"
                v-model="tempCoupon.percent" placeholder="請輸入折扣百分比">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled" id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary"
              @click="updateCoupon">更新優惠券</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 刪除 Modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempCoupon.title }}</strong> 優惠券(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon(tempCoupon.id)">確認刪除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination"

export default {
  data() {
    return{
      coupons: [],
      tempCoupon: {},
      isNew: true,
      isLoading: false,
      // status: {
      //   fileUploading: false,
      // },
      pagination: {},
      due_date: '2019/',
    }
  },
  components: {
    Pagination
  },
  methods: {
    // 「取得優惠券列表」
    getCoupons(page) {
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/coupons/?page=${page}`;
			this.$http.get(api).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.coupons = response.data.coupons;
          this.pagination = response.data.pagination;
          this.isLoading = false;
				}
			});
    },
    // 「開啟編輯/新增優惠券 Modal」
    openModal(isNew, item) {
      if (isNew){
        this.tempCoupon = {};
        this.due_date = new Date(Date.now()).yyyymmdd();
        this.isNew = true;
      }else{
        // 用assign避免物件傳參考特性 (否則列表顯示的item會被改掉...)  // this.tempProduct = item;
        this.tempCoupon = Object.assign({},item);
        this.due_date = new Date(this.tempCoupon.due_date).yyyymmdd();
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    openDelModal(item) {
      this.tempCoupon = Object.assign({},item);
      $("#delProductModal").modal("show");
    },
    // 「更新優惠券資訊」(新增/編輯後)
    updateCoupon() {
       let vm = this;
      // 先把due_date轉換一下
      // console.log(vm.due_date);
      // console.log (new Date().getTime(vm.due_date));
      vm.tempCoupon.due_date = new Date(vm.due_date).getTime();

      let api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = "post";
      // 若為「編輯」，替換api路徑、方法
      if (!vm.isNew){
        // 編輯才有id (建立後從後端抓回來的id)
        api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempCoupon.id}`;
        httpMethod = "put"
      }
      this.$http[httpMethod](api, {data: vm.tempCoupon}).then(response => {
        if (response.data.success){
          $("#couponModal").modal("hide");
          // 重新取得優惠券列表
          vm.getCoupons();
        }
      });
    },
    deleteCoupon(id) {
      let vm = this;
      let api= `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/coupon/${id}`;
      this.$http.delete(api).then(response => {
        console.log(response.data);
        if (response.data.success){
          $("#delProductModal").modal("hide");
          // 重新取得商品列表
          vm.getCoupons();
        }
      });
    },
  },
  created() {
    this.getCoupons()
  }
}
</script>


