<template>
  <div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>
    <!-- 新增商品按鈕 -->
    <div class="text-right">
      <button class="btn btn-primary mt-4" @click="openModal(true)">建立新的產品</button>
    </div>
    <!-- 商品列表 -->
    <table class="table mt-4">
      <thead>
        <th width="150">類別</th>
        <th>產品名稱</th>
        <th width="150">原價</th>
        <th width="150">售價</th>
        <th width="120">是否啟用</th>
        <th width="120">編輯</th>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price | currency }}</td>
          <td>{{ item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
          <div class="btn-group" role="group" aria-label="Basic example">
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)">編輯</button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelModal(item)">刪除</button>
          </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- 分頁 -->
    <!-- <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre}">
          <a class="page-link" href="#" aria-label="Previous"
          @click.prevent="getProducts(pagination.current_page-1)">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page"
        :class="{'active': pagination.current_page == page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <li class="page-item" :class="{'disabled': !pagination.has_next}">
          <a class="page-link" href="#" aria-label="Next" 
          @click.prevent="getProducts(pagination.current_page+1)">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav> -->
    <Pagination v-if="pagination.total_pages>1" :pagination="pagination" @pageChange='getProducts'></Pagination>
    <!-- 新增/修改 Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" 
    aria-labelledby="exampleModalLabel" aria-hidden="true" >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input type="text" class="form-control" id="image"
                    placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile">或 上傳圖片
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile" value="">
                </div>
                <!-- <img src="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" alt=""> -->
                  <img :src="tempProduct.imageUrl" class="img-fluid" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input type="text" class="form-control" id="title"
                    placeholder="請輸入標題" v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input type="text" class="form-control" id="category"
                      placeholder="請輸入分類" v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="請輸入單位" v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price">原價</label>
                    <input type="number" class="form-control" id="origin_price"
                      placeholder="請輸入原價" v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input type="number" class="form-control" id="price"
                      placeholder="請輸入售價" v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="請輸入產品說明內容" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled" v-model="tempProduct.is_enabled" 
                      true-value="1" false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 刪除 Modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteProduct(tempProduct.id)">確認刪除</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination"

export default {
  data() {
    return{
      products: [],
      tempProduct: {},
      isNew: true,
      isLoading: false,
      status: {
        fileUploading: false,
      },
      pagination: {},
    }
  },
  components: {
    Pagination
  },
  methods: {
    // 「取得商品列表」
    getProducts(page) {
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/products/?page=${page}`;
			this.$http.get(api).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.products = response.data.products;
          this.pagination = response.data.pagination;
          this.isLoading = false;
				}
			});
    },
    // 「開啟編輯/新增 Modal」
    openModal(isNew, item) {
      // 清空上傳圖框的旁的檔案名稱文字
      this.$refs.files.value = "";

      if (isNew){
        this.tempProduct = {};
        this.isNew = true;
      }else{
        // 用assign避免物件傳參考特性 (否則列表顯示的item會被改掉...)  // this.tempProduct = item;
        this.tempProduct = Object.assign({},item);
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    openDelModal(item) {
      this.tempProduct = Object.assign({},item);
      $("#delProductModal").modal("show");
    },
    // 「更新商品資訊」(新增/編輯後)
    updateProduct() {
      let vm = this;
      let api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = "post";
      // 若沒有填值，自動補上
      if(!vm.tempProduct.price){
        vm.tempProduct.price = vm.tempProduct.original_price;
      }
      if(!vm.tempProduct.unit){
        vm.tempProduct.unit = "個";
      }

      // 若為「編輯」，替換api路徑、方法
      if (!vm.isNew){
        // 編輯才有id (建立後從後端抓回來的id)
        api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = "put"
      }
      this.$http[httpMethod](api, {data: vm.tempProduct}).then(response => {
        if (response.data.success){
          $("#productModal").modal("hide");
          // 重新取得商品列表
          vm.getProducts();
        }
      });
    },
    deleteProduct(id) {
      let vm = this;
      let api= `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/product/${id}`;
      this.$http.delete(api).then(response => {
        console.log(response.data);
        if (response.data.success){
          $("#delProductModal").modal("hide");
          // 重新取得商品列表
          vm.getProducts();
        }
      });
    },
    // 「上傳圖片」
    uploadFile() {
      // 取得檔案，並以裝成fomrData格式
      var files = this.$refs.files.files[0];
      if (files) {
        this.status.fileUploading = true;
      }
      var formData = new FormData();
      formData.append('file-to-upload', files);
      // AJAX 傳資料 (以formData格式傳送)
      let vm = this;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http.post(api, formData, {
        header: {'Content-Type' : "multipart/form-data"}
      })
      .then(response => {
        if (response.data.success){
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
        }else{
          // 產生錯誤訊息框(EventBus呼叫)
          vm.$bus.$emit('message:push', response.data.message, 'danger');
        }
        vm.status.fileUploading = false;
      });
    },
  },
  // 載入頁面就AJAX取得商品列表
  created() {
    this.getProducts();
  },
  // 若要使用JQeruy綁事件，使用mounted (複習)
  // mounted(){
  //   let vm = this;
  //   $('#productModal').on('hidden.bs.modal', function (e) {
  //     vm.$refs.files.value = "";
  //   })
  // }
}
</script>



