<template>
  <div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>

    <!-- 商品列表 -->
    <div class="row my-5">
      <!-- 整個卡片 -->
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm h-100">
          <div style="height: 150px; background-size: cover; background-position: center"
          :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark" @click.prevent="getProduct(item.id)">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.description }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="item.price==item.origin_price">售價 {{ item.origin_price }}元</div>
              <del class="h6" v-if="item.price!==item.origin_price">原價 {{ item.origin_price }} 元</del>
              <div class="h5" v-if="item.price!==item.origin_price">現在只要 {{ item.price }} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.addingCart === item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- 單一商品Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="text-center">
              <img :src="product.imageUrl" class="img-fluid" alt="">
            </div>
            <!-- <img :src="product.imageUrl" class="w-100" alt=""> -->
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
              <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
            </div>
            <select name="" class="form-control mt-3" v-model="selectNum">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計 <strong>{{ selectNum * (product.price||product.origin_price) }}</strong> 元
            </div>
            <button type="button" class="btn btn-primary"
              @click="addtoCart(product.id, selectNum)">
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- 購物車列表 -->
    <h4 class="text-center mt-5" v-if="cart.carts && cart.carts.length">我的購物車</h4>
    <div class="mb-5 row justify-content-center" v-if="cart.carts && cart.carts.length">
      <div class="mt-4 col-md-8">
        <table class="table">
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  已套用優惠券
                </div>
              </td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total |currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total |currency }}</td>
            </tr>
            <tr v-if="cart.total !== cart.final_total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total |currency }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="coupon">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
              套用優惠碼
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- 購物車訂單表單 -->
    <div class="my-5 row justify-content-center" v-if="cart.carts && cart.carts.length">
      <form class="col-md-6" @submit.prevent="createOrder">
        <div class="form-group">
          <label for="useremail">*Email</label>
          <input type="email" class="form-control" name="email" id="useremail"
            v-model="form.user.email" placeholder="請輸入 Email" v-validate="'required'">
          <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
        </div>
      
        <div class="form-group">
          <label for="username">*收件人姓名</label>
          <input type="text" class="form-control" name="name" id="username"
            v-model="form.user.name" placeholder="輸入姓名" v-validate="'required'">
          <span class="text-danger" v-if="errors.has('name')">請輸入收件人姓名</span>
        </div>
      
        <div class="form-group">
          <label for="usertel">收件人電話</label>
          <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
        </div>
      
        <div class="form-group">
          <label for="useraddress">*收件人地址</label>
          <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
            placeholder="請輸入地址" v-validate="'required'">
          <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
        </div>
      
        <div class="form-group">
          <label for="comment">留言</label>
          <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger">送出訂單</button>
        </div>
      </form>
    </div>

  </div>
</template>
<script>
import $ from "jquery";

export default {
 data() {
   return{
     isLoading: false,
     products: [],
     product: {},
     status:{
       loadingItem: '',
       addingCart: '',
    },
    selectNum: 1,
    cart: [],
    coupon: '',
    form: {
      user: {
        name: '',
        email: '',
        tel: '',
        address: ''
      },
      message: '',
    }
   }
 },
 methods: {
   // 用戶「取得商品列表」
    getProducts(page) {
      this.isLoading = true;
      // const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/products/?page=${page}`;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/products/all`;
			this.$http.get(api).then(response => {
        // console.log(response.data);
				if (response.data.success){
          this.products = (response.data.products).reverse();
          // this.pagination = response.data.pagination;
          this.isLoading = false;
				}
      });
    },
    // 用戶「取得單一商品細節」
    getProduct(id) {
      this.status.loadingItem = id;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/product/${id}`;
			this.$http.get(api).then(response => {
        // console.log(response.data);
				if (response.data.success){
          this.product = response.data.product;
          this.selectNum = 1;
          $("#productModal").modal("show");
          this.status.loadingItem = '';
				}
      });
    },
    // 用戶「加入購物車」(要傳參數)
    addtoCart(id, qty=1) {
      this.status.addingCart = id;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/cart`;
      let cart = {
        product_id: id,
        qty,
      };
			this.$http.post(api, {data: cart}).then(response => {
        console.log(response.data);
				if (response.data.success){
          $("#productModal").modal("hide");
          // 重新從後端取得購物車內容，但為何不是把回傳的資料加入前端products?
          this.getCart();
          this.status.addingCart = '';
				}
      });
    },
    // 「取得購物車列表」
    getCart() {
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/cart`;
			this.$http.get(api).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.cart = response.data.data;
          this.isLoading = false;
				}
      });
    },
    // 「刪除購物車商品」 (帶id參數)
    removeCartItem(id) {
      // this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/cart/${id}`;
			this.$http.delete(api).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.getCart();
          // this.isLoading = false;
				}
      });
    },
    // 「套用優惠券」
    addCouponCode() {
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/coupon`;
      let coupon = {
        code: this.coupon,
      };
			this.$http.post(api, {data: coupon}).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.getCart();
          this.isLoading = false;
				}
      });
    },
    // 「送出訂單」
    createOrder() {
      let vm = this;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/order`;
      let coupon = {
        code: this.coupon,
      };
      this.$validator.validate().then(valid => {
        if (valid) {
          this.isLoading = true;
          // 驗證沒問題，才要送出AJAX
          this.$http.post(api, {data: vm.form}).then(response => {
            console.log(response.data);
            if (response.data.success){
              this.isLoading = false;
            }
          });
        }
      });
			
    }
  },
  created() {
    this.getProducts();
    this.getCart();
  }
}
</script>


