<template>
  <div class="hello">
    <form class="form-signin my-5" @submit.prevent="signin">
      <h1 class="h3 mb-3 font-weight-normal">管理者登入</h1>
      <label for="inputEmail" class="sr-only">信箱</label>
      <input
        type="email"
        id="inputEmail"
        class="form-control"
        placeholder="信箱"
        required
        autofocus
        v-model="user.username"
      >
      <label for="inputPassword" class="sr-only">密碼</label>
      <input
        type="password"
        id="inputPassword"
        class="form-control"
        placeholder="密碼"
        required
        v-model="user.password"
      >
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me"> 記住我
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">登入</button>
      <p class="mt-5 mb-3 text-muted">&copy; 2018-2019</p>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      // api文件裡，post的參數物件，v-model到HTML上
      user: {
        username: "",
        password: "",
      }
    };
  },
  methods: {
    signin() {
			const api = `${process.env.APISERVER}/admin/signin`;
			// 登入API是使用 post方法，要傳參數(user物件)
			var vm = this;
			this.$http.post(api, vm.user).then(response => {
				console.log(response.data);
				if (response.data.success){
					// 登入成功則會轉跳到主頁
					vm.$router.push("/admin/products")
				}else{
					console.log("登入失敗");
				}
			});
		}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



