<template>
  <div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>
    <!-- 訂單列表 -->
    <table class="table mt-5">
      <thead>
        <th width="150">購買時間</th>
        <th>Email</th>
        <th width="250">購買款項</th>
        <th width="150">應附金額</th>
        <th width="120">是否付款</th>
        <!-- <th width="120">修改訂單</th> -->
      </thead>
      <tbody>
        <tr v-for="item in orders" :key="item.id">
          <td>{{ (item.create_at)*1000|date }}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <div v-for="p in item.products" :key="p.product_id">
              {{ p.product.title }}：{{ p.qty }} {{ p.product.unit }}
            </div>
          </td>
          <td>{{ item.total }}</td>
          <td>
            <span v-if="item.is_paid" class="text-success">已附款</span>
            <span v-else>未付款</span>
          </td>
          <!-- <td><button class="btn btn-outline-danger btn-sm">修改訂單</button></td> -->
        </tr>
      </tbody>
    </table>
    <!-- 分頁元件 -->
    <Pagination v-if="pagination.total_pages>1" :pagination="pagination" @pageChange='getOrders'></Pagination>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination"

export default {
  data() {
    return{
      isLoading: false,
      orders: [],
      pagination: {},
    }
  },
  components: {
    Pagination
  },
  methods: {
    // 「取得訂單列表」
    getOrders(page) {
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/admin/orders/?page=${page}`;
			this.$http.get(api).then(response => {
        console.log(response.data);
				if (response.data.success){
          this.orders = response.data.orders;
          this.pagination = response.data.pagination;
          this.isLoading = false;
				}
			});
    },
  },
  created() {
    this.getOrders();
  }
}
</script>


